body {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  background: linear-gradient(37.72deg, #003D85 20.6%, #B80027 76.31%);
  background-size: auto 658px;
  background-repeat: no-repeat;

}

.bg-wrap-top {

}

.logo {
  text-decoration: none;
}

.logo:hover {
  opacity: 70%;
}

.logo:active {
  opacity: 50%;
}

.button {
  list-style: none;
  text-decoration: none;
  outline: none;
  border: none;
  cursor: pointer;
}

.button:hover {
  opacity: 70%;
}

.button:active {
  opacity: 50%;
}

@media (max-width: $desktop-width) {
  body {
    background-size: auto 554px;
  }
}


@media (max-width: $tablet-width) {
  body {
    background-size: auto 491px;
  }
}
